import { mapActions, mapGetters } from 'vuex';
import Pencil from 'vue-material-design-icons/Pencil.vue';
import Close from 'vue-material-design-icons/Close.vue';
import AlertCircle from 'vue-material-design-icons/AlertCircle.vue';
import Star from 'vue-material-design-icons/Star.vue';
import Download from 'vue-material-design-icons/Download.vue';
import Send from 'vue-material-design-icons/Send.vue';
import Merge from 'vue-material-design-icons/Merge.vue';
import ArrowLeft from 'vue-material-design-icons/ArrowLeft.vue';
import AccountSwitch from 'vue-material-design-icons/AccountSwitch.vue';
import AccountBoxMultipleOutline from 'vue-material-design-icons/AccountBoxMultipleOutline.vue';
import { NcActions, NcActionButton, NcModal, NcEmptyContent, NcButton, NcLoadingIcon } from '@nextcloud/vue';
import FetchFilesMixin from '../mixins/FetchFilesMixin.js';
import FilesSelectionMixin from '../mixins/FilesSelectionMixin.js';
import FilesListViewer from '../components/FilesListViewer.vue';
import File from '../components/File.vue';
import logger from '../services/logger.js';
import FetchFacesMixin from '../mixins/FetchFacesMixin.js';
import Vue from 'vue';
import FaceMergeForm from '../components/Faces/FaceMergeForm.vue';
export default {
  name: 'FaceContent',
  components: {
    Pencil,
    Star,
    Download,
    Close,
    AlertCircle,
    Send,
    Merge,
    ArrowLeft,
    AccountBoxMultipleOutline,
    FaceMergeForm,
    FilesListViewer,
    File,
    NcLoadingIcon,
    NcEmptyContent,
    NcActions,
    NcActionButton,
    NcModal,
    NcButton,
    AccountSwitch
  },
  directives: {
    focus(el) {
      Vue.nextTick(() => el.focus());
    }
  },
  mixins: [FetchFacesMixin, FetchFilesMixin, FilesSelectionMixin],
  props: {
    faceName: {
      type: String,
      default: '/'
    }
  },
  data() {
    return {
      showMoveModal: false,
      showMergeModal: false,
      showRenameModal: false,
      loadingCount: 0,
      appContent: document.getElementById('app-content-vue')
    };
  },
  computed: {
    ...mapGetters(['files', 'facesFiles']),
    /**
     * @return {string[]} The face information for the current faceName.
     */
    face() {
      return this.faces[this.faceName];
    },
    /**
     * @return {string[]} The list of files for the current faceName.
     */
    faceFileIds() {
      return this.facesFiles[this.faceName] || [];
    },
    /** @type {boolean} */
    shouldFavoriteSelection() {
      // Favorite all selection if at least one file is not on the favorites.
      return this.selectedFileIds.some(fileId => this.$store.state.files.files[fileId].favorite === 0);
    }
  },
  watch: {
    face() {
      if (this.face) {
        this.fetchFaceContent(this.faceName);
      }
    }
  },
  mounted() {
    this.fetchFaceContent(this.faceName);
  },
  methods: {
    ...mapActions(['appendFiles', 'deleteFace', 'renameFace', 'downloadFiles', 'toggleFavoriteForFiles', 'removeFilesFromFace', 'moveFilesToFace']),
    openViewer(fileId) {
      const file = this.files[fileId];
      OCA.Viewer.open({
        // remove /username/files/ from the start
        path: '/' + file.filename.split('/').slice(3).join('/'),
        list: this.faceFileIds.map(fileId => ({
          ...this.files[fileId],
          basename: this.files[fileId].basename.split('-').slice(1).join('-')
        })).filter(file => !file.sectionHeader),
        loadMore: file.loadMore ? async () => await file.loadMore(true) : () => [],
        canLoop: file.canLoop
      });
    },
    async handleRemoveFilesFromFace(fileIds) {
      try {
        this.loadingCount++;
        await this.removeFilesFromFace({
          faceName: this.faceName,
          fileIdsToRemove: fileIds
        });
        this.resetSelection();
      } catch (error) {
        logger.error(error);
      } finally {
        this.loadingCount--;
      }
    },
    async handleDeleteFace() {
      try {
        this.loadingCount++;
        await this.deleteFace({
          faceName: this.faceName
        });
        this.$router.push('/faces');
      } catch (error) {
        logger.error(error);
      } finally {
        this.loadingCount--;
      }
    },
    async handleRenameFace(faceName) {
      try {
        this.loadingCount++;
        this.showRenameModal = false;
        const oldName = this.faceName;
        await this.renameFace({
          oldName,
          faceName
        });
        this.$router.push({
          name: 'facecontent',
          params: {
            faceName
          }
        });
      } catch (error) {
        logger.error(error);
      } finally {
        this.loadingCount--;
      }
    },
    async handleMerge(faceName) {
      try {
        this.loadingCount++;
        await this.moveFilesToFace({
          oldFace: this.faceName,
          faceName,
          fileIdsToMove: this.facesFiles[this.faceName]
        });
        await this.deleteFace({
          faceName: this.faceName
        });
        this.showMergeModal = false;
        this.$router.push({
          name: 'facecontent',
          params: {
            faceName
          }
        });
      } catch (error) {
        logger.error(error);
      } finally {
        this.loadingCount--;
      }
    },
    async handleMove(faceName, fileIds) {
      try {
        this.loadingCount++;
        await this.moveFilesToFace({
          oldFace: this.faceName,
          faceName,
          fileIdsToMove: fileIds
        });
        this.showMoveModal = false;
      } catch (error) {
        logger.error(error);
      } finally {
        this.loadingCount--;
      }
    },
    async favoriteSelection() {
      try {
        this.loadingCount++;
        await this.toggleFavoriteForFiles({
          fileIds: this.selectedFileIds,
          favoriteState: true
        });
      } catch (error) {
        logger.error(error);
      } finally {
        this.loadingCount--;
      }
    },
    async unFavoriteSelection() {
      try {
        this.loadingCount++;
        await this.toggleFavoriteForFiles({
          fileIds: this.selectedFileIds,
          favoriteState: false
        });
      } catch (error) {
        logger.error(error);
      } finally {
        this.loadingCount--;
      }
    },
    async downloadSelection() {
      try {
        this.loadingCount++;
        await this.downloadFiles(this.selectedFileIds);
      } catch (error) {
        logger.error(error);
      } finally {
        this.loadingCount--;
      }
    }
  }
};